"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { SearchBar } from "@/components/toolbar/SearchBar"
import { observer } from "mobx-react-lite"
import { useStore } from '@/stores/StoreProvider'
import CustomersTable from "./customerList/customersTable"
import { toast } from "sonner"

// Select components
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

const mockCustomers = [
  { id: 1, name: "Amit Kumar", upiId: "amit@upi", stamps: 12, visits: 15, lastVisit: "2 days ago" },
  { id: 2, name: "Priya Singh", upiId: "priya@upi", stamps: 8, visits: 10, lastVisit: "1 day ago" },
  { id: 3, name: "Vikram Patel", upiId: "vikram@upi", stamps: 5, visits: 6, lastVisit: "3 days ago" },
  { id: 4, name: "Neha Sharma", upiId: "neha@upi", stamps: 20, visits: 25, lastVisit: "Today" },
  { id: 5, name: "Rahul Verma", upiId: "rahul@upi", stamps: 3, visits: 2, lastVisit: "4 days ago" },
  { id: 6, name: "Sita Devi", upiId: "sita@upi", stamps: 15, visits: 12, lastVisit: "1 week ago" },
]

const CustomersList = () => {

  const { shopStore } = useStore()
  const [search, setSearch] = useState("")
  const [currentPage, setCurrentPage] = useState(1)
  const [itemsPerPage, setItemsPerPage] = useState(5)
  const [customers, setCustomers] = useState([])
  const [page, setPage] = useState(1)
  const [limit, setLimit] = useState(5)

  const [totalPages, setTotalPages] = useState(1)
  const [totalItems, setTotalItems] = useState(0)


  // FETCH DATA
  useEffect(() => {

    const fetchCustomers = async () => {

      const data = await shopStore.fetchCustomers({page, limit, search})

      if (shopStore.error) {
        toast.error(shopStore.error)
        setCustomers([])
        return
      }

      setCustomers(data.data);

    }

    fetchCustomers()
  }, [page, limit, search])


  const startIndex = (currentPage - 1) * itemsPerPage


  const goToPage = (pageNum) => {
    if (pageNum >= 1 && pageNum <= totalPages) {
      setCurrentPage(pageNum)
    }
  }

  return (
    <div className="space-y-2">

      {/* SEARCH BAR */}
      <div className="flex justify-between">
        <SearchBar
          value={search}
          onChange={(value) => {
            setSearch(value)
            setCurrentPage(1)
          }}
          placeholder="Search by name or phone..."
        />
      </div>

      {/* TABLE */}
      <CustomersTable customers={customers} />

      {/* PAGINATION + ITEMS PER PAGE */}
      <div className="flex justify-between items-center pt-2">

        {/* Showing text */}
        <div className="text-sm text-muted-foreground">
          Showing {startIndex + 1}â€“
          {Math.min(startIndex + itemsPerPage, totalItems)} of {totalItems}
        </div>

        <div className="flex items-center gap-4">

          {/* ITEMS PER PAGE SELECTOR */}
          <div className="flex items-center gap-2">
            <span className="text-sm text-muted-foreground">Rows per page:</span>

            <Select
              value={String(itemsPerPage)}
              onValueChange={(value) => {
                setItemsPerPage(Number(value))
                setCurrentPage(1)
              }}
            >
              <SelectTrigger className="w-[80px]">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="5">5</SelectItem>
                <SelectItem value="10">10</SelectItem>
                <SelectItem value="20">20</SelectItem>
                <SelectItem value="50">50</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* PAGINATION BUTTONS */}
          <div className="flex items-center gap-2">

            <Button
              variant="outline"
              size="sm"
              disabled={currentPage === 1}
              onClick={() => goToPage(currentPage - 1)}
            >
              Prev
            </Button>

            {[...Array(totalPages)].map((_, i) => (
              <Button
                key={i}
                size="sm"
                variant={currentPage === i + 1 ? "default" : "outline"}
                onClick={() => goToPage(i + 1)}
              >
                {i + 1}
              </Button>
            ))}

            <Button
              variant="outline"
              size="sm"
              disabled={currentPage === totalPages}
              onClick={() => goToPage(currentPage + 1)}
            >
              Next
            </Button>

          </div>
        </div>
      </div>
    </div>
  )
}

export default observer(CustomersList)